import { IDarwinShare, IAppShare,IWXMiniShare } from "../I/IShareOption";
class ShareConfig {
    protected _snappshare:IAppShare;
    protected _darwinshare:IDarwinShare;
    protected _minishare:IWXMiniShare;
    public set snAppShare(opt:IAppShare){
        this._snappshare=opt;
    }
    public set darwinShare(opt:IDarwinShare){
       
        this._darwinshare=opt;
        
    }
    public set miniShare(opt:IWXMiniShare){
        this._minishare=opt;
    }
    public get snAppShare():IAppShare{return this._snappshare};
    public get darwinShare():IDarwinShare{return this._darwinshare};
    public get miniShare():IWXMiniShare{return this._minishare};
    
}
export {ShareConfig}