import {IRequest,REQUEST_METHOD} from '../I/IRequest'
import {IRequestOption} from '../I/IResquestOption';
import getCallBackName from '../utils/callbackManager'
import { ScriptLoader } from '../utils/ScriptLoader';
import { IResponse } from '../I/IResponse';
//
class HttpRequest {
    constructor(){}
    public async request(params:IRequest,option:IRequestOption=null):Promise<IResponse>{
        
        let result= await this.doRequest(params,option);
        if(option && option.beforeOutPut)
        {
            result=option.beforeOutPut.call(null,result);
        }
        return result
    }
    private doRequest(params:IRequest,option:IRequestOption=null):Promise<IResponse>{
        return new Promise((ros:Function,jet:Function)=>{
           
            const xhr=new XMLHttpRequest();
            const {url,method,dataType,data}=params;
            xhr.onreadystatechange=()=>{
               
               if(xhr.readyState!=4) return;
                //console.log(xhr);
               let response:string | Error = this.parseResult(xhr);
               if(response==null) return;
               
               if(response instanceof Error) {
                   jet(response);
                   return;
               }
              
               switch(dataType)
               {
                   case "json":
                        
                        response=JSON.parse(<string>response);
                   break;
                   default:
                        response=response;
                       break;
               }
               ros(response);
            };
            xhr.onload=(e)=>{};
            xhr.onabort=()=>{};
            xhr.onerror=(res)=>{jet(res)};
            
            let apiurl=this.buildData(params);
            
            switch(params.dataType)
            {
                case 'jsonp':
                    const CallbackFunction:string=getCallBackName((res:any)=>{
                        ros(res);
                    });
                    apiurl=url+"?"+apiurl+"&callback="+CallbackFunction;
                    ScriptLoader.load(apiurl).then(()=>{
                        //执行后删除script标签
                       (<HTMLScriptElement> ScriptLoader.scriptPool.get(apiurl)).remove();
                    })
                    break;
                default:
                    switch(method)
                    {
                        case "get":
                                xhr.open(method,url+"?"+apiurl);
                                if(option && option.beforeSend)
                                {
                                    option.beforeSend.call(null,xhr,params);
                                }
                                xhr.send();
                            break;
                        case "post":
                                xhr.open(method,url);
                                xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
                                if(option && option.beforeSend)
                                {
                                    option.beforeSend.call(null,xhr,params);
                                }
                                xhr.send(apiurl);
                            break;
                            //postform,post 表单
                        case "postform":
                                xhr.open('post',url);
                                if(option && option.beforeSend)
                                {
                                    option.beforeSend.call(null,xhr,params);
                                }
                                xhr.send(params.data);
                            break;
                    }
                   
                    break;
            }
           
        })
    }
    private buildData(params:IRequest):string{
        const data=params.data;
        return this.makeObjectString(data);
    }
    private makeObjectString(obj:any):string{
        obj=<Object>(obj);
        let str="";
        let arr=Object.entries(obj).map((item:Array<any>,index:number)=>{
            return item.join('=');
        });
        str=arr.join("&");
        return str;

    }
    private parseResult(res:XMLHttpRequest){
        switch(res.status)
        {
            case 404:
                return new Error('404');
            break;
            case 200:
                return res.responseText;
            break;
        }
        
        return null;
       
    }
}
export {HttpRequest}