import { AHero } from "./heros/AHero";
import { appShare } from "./heros/appShare";
import { ScriptLoader } from "./utils/ScriptLoader";
import { resource } from "./config/resource";
import { wxbridge } from "./heros/wxbridge";
import {ShareConfig} from './doShareConfig/ShareConfig'
import { IShare } from "./I/IShareOption";
import deviceCharge from "./utils/deviceCharge";

interface ILocation{
    //经度
    longitude:number|string;
    //维度
    latitude:number|string;
}
interface Iuainfo{
    ua:string;
    location?:ILocation
}
class application{
    //活动ID：
    public activityCode:string;
    protected uainfo:Iuainfo;
    protected shareTarget:AHero;
    protected isdebug:boolean=false;
    protected shareConfig:ShareConfig;
    constructor(activityCode:string,isdebug:boolean){

        this.activityCode=activityCode;
        this.isdebug=isdebug;
        this.initDev().then((tar:AHero)=>{console.log(tar);this.shareTarget=tar});
        this.shareConfig=new ShareConfig();
        //console.log("操作系统信息:",deviceCharge())
    }
    //初始化的分享
    share(opton:IShare =null){
        this.shareTarget.makeShare(opton as IShare);
    }
    //单独调用分享
    doShare(opton:IShare =null){
        this.shareTarget.share(opton);
    }
    get shareconfig():ShareConfig{
        return this.shareConfig;
    }
    get debug():boolean{
        return this.isdebug;
    }
    initDev():Promise<{}>{
        //获取设备信息
        console.log('获取设备信息')
        return new Promise((ros:Function,jet:Function)=>{
            const ua:string=window.navigator.userAgent;
            this.uainfo={
                ua,
            };
            console.log(ua);
            if(ua.indexOf('SNEBUY-APP')>=0)
            {
                console.log('苏宁易购客户端');
                ros(new appShare(this));
            }
            else{
                console.log('加载微信jssdk');
                
                ScriptLoader.load(resource.wxjssdk).then(()=>{
                 
                    ros(wxbridge.startConfig(this));
                    
                })
            }
        });
      
    }
    
}
export {application}