import { RELoader } from "./Loader";


class ScriptLoader extends RELoader
{
    //全局静态变量，方便管理script资源;
    static scriptPool:Map<string,HTMLScriptElement>=new Map();
    constructor(){
        super();
    }
    
    static load(src:string):Promise<{}>{
        
        return new Promise((ros:Function,jet:Function)=>{
            const sc=document.createElement('script');
            document.head.appendChild(sc);
            sc.onload=()=>{
                ros();
                ScriptLoader.scriptPool.set(src,sc);
            };
            sc.src=src;
        });  
    }
    static loadStyle(src:string):Promise<{}>{
        return new Promise((ros:Function,jet:Function)=>{
            const sc=document.createElement('link');
            sc.rel='stylesheet';
            document.head.appendChild(sc);
            sc.href=src;
        });  
    }

}
export {ScriptLoader}