//获取接口的域名地址
const getActHosts = ():string=>{
    const env:string=getEnv();
    switch(env)
    {
        case 'xgpre':
            return '//actprexg.cnsuning.com/'; 
            break;
        case 'pre':
            return "//actprexg.cnsuning.com/";
            break;
        default:
            return "//act.suning.com/"
            break;
    }
}
const getActGameHost=():string=>{
    //http://gameapi.suning.com/
    const env:string=getEnv();
    switch(env)
    {
        case 'xgpre':
            return '//apiprexg.game.cnsuning.com/'; 
            break;
        case 'pre':
            return '//apipre.game.cnsuning.com/'; 
            break;
        default:
            return '//gameapi.suning.com/'; 
            break;
    }
}
//判断当前环境，默认标准为根据url去判断
const getEnv =():string=> {
    const location=window.location.href;
    //新港测试
    if(location.indexOf('xgpre')>=0 || location.indexOf('prexg')>=0)
    {
        return 'xgpre';
    }
    //徐庄pre
    else if(location.indexOf('pre')>=0)
    {
        return 'pre';
    }
    //正式
    else{
        return 'prd';
    }
}
function getUploadApi(){
    /** 
     *  'PRE': '//mfspre.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do',
        'XGPRE': '//mfsxgpre.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do',
        'PRD': '//mfs.suning.com/mfs-web/photoActivity/private/androidUpload.do',
        'SIT': '//mfssit.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do',
        'DEV': '//mfsxgpre.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do'
    */
    const env:string=getEnv();
    switch(env)
    {
        case 'xgpre':
            return '//mfsxgpre.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do'; 
            break;
        case 'pre':
            return '//mfspre.cnsuning.com/mfs-web/photoActivity/private/androidUpload.do'; 
            break;
        default:
            return '//mfs.suning.com/mfs-web/photoActivity/private/androidUpload.do'; 
            break;
    }
}

function getShortApi(){
    const env:string=getEnv();
    //本地开发环境
    if(location.port=='9000')
    return "http://localhost:3000/proxy/xgpre/"
    switch(env)
    {
        case 'xgpre':
            return '/proxy/xgpre/'; 
            break;
        case 'pre':
            return '/proxy/pre/'; 
            break;
        default:
            return '/proxy/prd/'; 
            break;
    }
    /*switch(env)
    {
        case 'xgpre':
            return '/proxy/'; 
            break;
        case 'pre':
            return '//mpbsspre.cnsuning.com/mpbss-web/comm/toShort.do'; 
            break;
        default:
            return '//mpbss.suning.com/mpbss-web/comm/toShort.do'; 
            break;
    }*/

}

const apiresource = {
    host:getActHosts(),
    wxjssdktoken:"act-wap-web/wap/public/getWechatToken.htm",
    gamehost:getActGameHost(),
    getDarwinShare:"sngame-web/api/queryH5ShareInfo.do",
    getMiniShare:"sngame-web/api/getShareWeChatInfo.do",
    getAppShare:"sngame-web/api/getShareLongPicInfo.do",
    //小程序的分享承载页
    miniprogrampage:'/packageC/pages/commonShare/share',
    //短链接接口
    shortapi:getShortApi(),
    uploadApi:getUploadApi()
    
}
export {apiresource}