import { NetNode } from "./netNode";
import { apiresource } from "../config/apiresource";
class AppNetNode extends NetNode{
    constructor(){
        const apiurl:string=apiresource.gamehost+apiresource.getAppShare;
        super(apiurl);
    }
}
class DarwinNetNode extends NetNode{
    constructor(){
        const apiurl:string=apiresource.gamehost+apiresource.getDarwinShare;
        super(apiurl);
    }
}
class WxMiniNetNode extends NetNode{
    constructor(){
        const apiurl:string=apiresource.gamehost+apiresource.getMiniShare;
        super(apiurl);
    }
}
export {AppNetNode,DarwinNetNode,WxMiniNetNode}