import * as   QRCode from 'qrcode';

interface IImageDraw{
    img:HTMLImageElement | HTMLCanvasElement | HTMLVideoElement;
    option:IImageDrawOption
}
interface IImageDrawOption{
    x:number;
    y:number;
}
class ImageInter{
    private canvas:HTMLCanvasElement;
    private context:CanvasRenderingContext2D;
    private _width:number=0;
    private _height:number=0;
    constructor(){
        this.canvas=document.createElement('canvas');
        this.context=this.canvas.getContext('2d');
        //console.log(QRCode);
    }
    /** 
     * 设置背景。并用背景的宽高作为整个合成元素的宽高
    */
    setBg(src:string){
        return new Promise((ros:Function,jet:Function)=>{
            const img:HTMLImageElement=new Image();
            img.setAttribute("crossOrigin",'*');
            img.src=src;
            img.onload=()=>{
                this.width=img.width;
                this.height=img.height;
                img.setAttribute("crossOrigin",'*');
                const drawi:IImageDraw={
                    img:img,
                    option:{
                        x:0,y:0
                    }
                }
                this.drawElement(drawi);
                console.log(this.toData);
                ros(this);
            }
            img.onerror=()=>{
                console.warn('绘制图像时可能遇到了跨域问题，请联系sdk开发方');
                console.warn('图像可能来自于:', src);
                console.warn('确保你当前的活动域名已经与以上图像域名做好关联');
                jet();
            }
        })
       
    }
    async getQrcode(str:string):Promise<string>
    {
        console.log({str})
        const url:string=await QRCode.toDataURL(str);
        return url;
    }

    async drawBase64(base64:string,x:number=0,y:number=0):Promise<IImageDraw>
    {
        const img:HTMLImageElement=new Image();
        img.setAttribute("crossOrigin",'*');
        return new Promise((res,jet)=>{
            img.onload=()=>{
                const idr:IImageDraw={
                    img,
                    option:{
                        x,
                        y
                    }  
                }   
                res(idr);             
            };
            //img.setAttribute("crossOrigin",'Anonymous');
            img.src=base64;
        })
       
        
        
    }
    /**
     *
     *
     * @param {IImageDraw} draw
     * @returns {ImageInter}
     * @memberof ImageInter
     * 绘制元素
     */
    drawElement(draw:IImageDraw):ImageInter
    {
        this.context.drawImage(draw.img,0,0,
            draw.img.width,
            draw.img.height,
            draw.option.x,
            draw.option.y,
            draw.img.width,
            draw.img.height);
        return this;
    }
    get toData():string{
        console.log(this.canvas);
        try{
            return this.canvas.toDataURL('image/jpeg',.8);
        }
        catch(e)
        {
            throw new Error('your drawing pic is in domain not allowed to assess');
           // console.log('')
        }
      
    }
    set width(val:number)
    {
        this._width=val;
    }
    get width():number{
        return this._width;
    }
    set height(val:number)
    {
        this._height=val;
    }
    get height(){
        return this._height;
    }
    

    addImage(){

    }
}
export {ImageInter}