import { application } from "./app";
import { IShare,IAppShare, IAPP_SHARE_CHANCEL, IWXMiniShare } from "./I/IShareOption";
import formtemp from './template/shareform'
class SNSHARE{
    static app:application;
    static init(acode:string,isdebug:boolean=false){
        SNSHARE.app=new application(acode,isdebug);
        return SNSHARE;
    }
    static share(opt:IShare=null){
        const {app} = SNSHARE;
        app.share(opt);
    }
    //分享至好友方法
    static ShareFriend(opt:IShare=null){
        //console.log('ShareFriend');

        if(opt) {
            opt=(opt as IAppShare);
            (opt as IAppShare).shareChancel=IAPP_SHARE_CHANCEL.friend;
        }
        else{
            opt=<IAppShare>{
                activityCode:SNSHARE.app.activityCode,
                shareChancel:IAPP_SHARE_CHANCEL.friend
            }
        }
        SNSHARE.app.doShare(opt)
    }
    //分享至朋友圈方法
    static ShareTimeLine(opt:IShare=null){
        if(opt) {
            opt=(opt as IAppShare);
            (opt as IAppShare).shareChancel=IAPP_SHARE_CHANCEL.timeline;
        }
        else{
            opt=<IAppShare>{
                activityCode:SNSHARE.app.activityCode,
                shareChancel:IAPP_SHARE_CHANCEL.timeline
            }
        }
        SNSHARE.app.doShare(opt)
    }
    //关闭分享弹窗
    static hideShare(){
        try{
            if(document.getElementsByClassName('lshare-overlay')[0])
            {   
                (document.getElementsByClassName('lshare-overlay')[0] as HTMLElement).style.display='none';
                (document.getElementsByClassName('lshare-overlay')[0] as HTMLElement).remove();
            }
        }
        catch(e)
        {
            console.warn("r",e)
        }
        
    }
    
    static showShare(){
        try{
            const div:HTMLDivElement=document.createElement('div');
            div.innerHTML = formtemp();
            document.body.appendChild(div.childNodes[0]);
            (document.getElementsByClassName('lshare-overlay')[0] as HTMLElement).style.display='block';
        }
        catch(e)
        {
            console.log(e);
        }
        
    }
    /**
     *
     *
     * @static
     * @param {IWXMiniShare} opt
     * @memberof SNSHARE
     */
    static setMiniProgramConfig(opt:IWXMiniShare){
        //console.log( SNSHARE.app.shareconfig);
        SNSHARE.app.shareconfig.miniShare={
            activityCode:SNSHARE.app.activityCode,
            sharelink:opt.sharelink
        }
    }
    static setWeChatProgramConfig(opt:IAppShare){
        SNSHARE.app.shareconfig.snAppShare=Object.assign(SNSHARE.app.shareconfig.snAppShare,{
            activityCode:SNSHARE.app.activityCode,
            sharelink:opt.sharelink,
            shareImg:'',
        });
    }
    static onShareSuccess(){
        //alert('123123')
    }
    static doShareSuccess(){
        console.log('分享成功');
        SNSHARE.hideShare();
        SNSHARE.onShareSuccess.call(SNSHARE);
    }
    static onShareFail(){
        //alert('0000')
    }
    static doShareFail(){
        console.log('分享失败');
        SNSHARE.hideShare();
        SNSHARE.onShareFail.call(SNSHARE);
    }
    
}
(<any> window).SNSHARE=SNSHARE;
export {SNSHARE}