import { AHero } from "./AHero";
import { application } from "../app";
import { IShare,IAppShare, IAPP_SHARE_CHANCEL, IAPP_SHARE_PATH, IWXMiniShare, IDarwinShare } from "../I/IShareOption";
import appshareform from "../template/shareform"
import { apiresource } from "../config/apiresource";
import { HttpRequest } from "../core/HttpRequest";
import { IResponse } from "../I/IResponse";
import { netManager } from "../netNode/netManager";
import { ScriptLoader } from "../utils/ScriptLoader";
import { resource } from "../config/resource";
import { imgShareConfig } from "../doShareConfig/AppShareConfig";
import { ImageInter } from "../core/ImageInter";
import { WxMiniNetNode } from "../netNode/ShareNetNode";

/**
 *
 * 
 *
 * @class appShare
 * @extends {AHero}
 * 苏宁易购小程序的分享逻辑
 * 1.加载易购分享配置
 * 2.判断配置项res.data.result.longPicShareSwitch 是否为1
 * +如果为1，则调用小程序分享配置
 * +不为1.继续易购 的分享模块
 */
enum APPSHARETYPE {
    towechat='towechat',
    tominiprogran='tomini',
    tonative='tonative'
}
class appShare extends AHero{
    //imgdrawer 为图片合成组件
    protected imgdrawer:ImageInter=new ImageInter();
    protected shareFunction:Function;
    protected shareType:string;
    constructor(app:application)
    {
        super(app);
        //QRCode.toDataURL('hello!').then((txt)=>{console.log(txt)});
        const {activityCode}=app;
        //加载苏宁前端jsssk
        ScriptLoader.load(resource.suningjsddk).then(()=>{
            
        });
        ScriptLoader.loadStyle(resource.sharestyle);
        var socialfun = this.getShareFunctionSocial();
        if (this.shareType === APPSHARETYPE.tonative) {
            this.initNativeConfig();
            this.shareFunction = this.getNativeFunction();
            return ;
          } //获取分享配置数据
        //获取分享配置数据
        netManager.appshare.doRequest({
            data:{
                activityCode,
                v:Math.random()*10
            }
        }).then((res:any)=>{
            console.log('获取分享配置数据成功',res.data.result);
            //判断小程序的分享是否开启。
            //longPicShareSwitch 为1 则用小程序的分享
            res.data.result.longPicShareSwitch=null;
            if(res.data.result.longPicShareSwitch && res.data.result.longPicShareSwitch=='1')
            {
                this.shareType=APPSHARETYPE.tominiprogran;
                this.shareFunction=this.shareToMini;
                //初始化小程序分享配置
                if(this.app.shareconfig.miniShare)
                {
                    this.app.shareconfig.miniShare=Object.assign(
                        this.app.shareconfig.miniShare,
                        {activityCode:this.app.activityCode}
                    )
                }
                else{
                    this.app.shareconfig.miniShare={
                        activityCode:this.app.activityCode,
                    }
                }   
                
                imgShareConfig.shareTo="小程序分享";
                //this.loadMiniConfig();
                //imgShareConfig.bgImg.src;
            }
            else{
                this.shareType=APPSHARETYPE.towechat;
                this.shareFunction=this.doAppShare;
                const config:IAppShare={
                    shareImg:res.data.result.longPicUrl,
                    x:res.data.result.longPicX,
                    y:res.data.result.longPicY
                };
                 //生成分享组件
                 //snapp分享配置
                app.shareconfig.snAppShare=Object.assign(config,app.shareconfig.snAppShare);
                imgShareConfig.bgImg.src=app.shareconfig.snAppShare.shareImg;
                 //分享弹窗组件相关的配置
                imgShareConfig.shareTo="微信分享";
                const opt:IShare=this.app.shareconfig.snAppShare;
                //给元素绘制对象设置背景。并确定了其宽高
                this.imgdrawer.setBg(app.shareconfig.snAppShare.shareImg);
            }
           
           
           
           
            //const div:HTMLElement=document.createElement('div');
            //div.innerHTML=appshareform();
            //document.body.appendChild(div.childNodes[0]);
           
           
        });
        console.log('SNNativeClient',(<any>window).SNNativeClient);
        console.log('shareApi',(<any>window).shareApi);
    }
    //初始化native 本地分享
    initNativeConfig(){
        
        console.log('初始化native 分享配置.');
        var activityCode = this.app.activityCode;
        netManager.darwinshare.doRequest({
            data: {
                activityCode: activityCode,
                v: Math.random() * 10
            }
            }).then( (res)=> {
              
                try {
                    this.app.shareconfig.darwinShare = res.data.result;
                  
                } catch (e) {
                    console.warn(e.toString());
                }
        });
    }
    //合成图片
    async makeShareImage(imgurl:String){
        //const img
        //const img:string=this.app.shareconfig.snAppShare.shareImg;
        const img:HTMLImageElement=new Image();
        const config:IAppShare=this.app.shareconfig.snAppShare;
        img.onload=()=>{
            this.imgdrawer.drawElement({
                img:img,
                option:{
                    x:config.x,
                    y:config.y
                }
            });
        };
        
    }
    //加载小程序的接口配置
    loadMiniConfig(){
        const mininode:WxMiniNetNode=new WxMiniNetNode();
    }
    initData(res:IResponse){
        
    }
    //调用苏宁易购本地的方向方法
    getNativeFunction():Function{
       
        if ((<any>window).SNNativeClient) return (<any>window).SNNativeClient.callNativeShare;
        else if ((<any>window).shareApi) return (<any>window).shareApi.callNativeShare;
        else return null; //return (<any>window).SNNativeClient.callNativeShare || (<any>window).shareApi.callNativeShare || null;

    }
    //获取social的分享方法
    getShareFunctionSocial():Function{
        //callMediaShare(imgurl,chancel,)
        try{
            if  ((<any>window).SNNativeClient) return (<any>window).SNNativeClient.callMediaShare;
            else if ((<any>window).shareApi) return (<any>window).shareApi.callMediaShare;
            else throw new Error('callMediaShare  noapi'); 
            //return (<any>window).SNNativeClient.callMediaShare || (<any>window).shareApi.callMediaShare

        }
        catch(e)
        {
            var netivefun = this.getNativeFunction();
            this.shareType = APPSHARETYPE.tonative;
            if (netivefun != null) {
              this.shareType = APPSHARETYPE.tonative;
            }
            return (opt:IShare)=>{
                console.log("分享api未获取");
                console.log(opt);
            }
        }
        
    }
    doShare(opt:IShare=null)
    {
        
        if(this.shareFunction)
        {
            this.shareFunction.call(this,opt)
        }
        else{
            console.warn('wrong!')
        }
        
    }
    async getShortLink(str:string):Promise<string>{
        const req:HttpRequest=new HttpRequest();
        const result:any=await req.request({
            url:apiresource.shortapi+encodeURIComponent(str),
            method:'get',
            dataType:'json',
            data:{
                longStr:str
            }
        });
        //console.log('qweqeqw',result);
        return result.data as string;
        
    }
    async shareToMini(opt:IWXMiniShare=null){
        //const sence:string=opt.
        //alert(opt.shareChancel);
        let net:WxMiniNetNode=new WxMiniNetNode();
        const config:IWXMiniShare=this.app.shareconfig.miniShare;
        
        const {activityCode}=this.app;
        const url:string=config.sharelink;
        console.log({
            opt
        });
        const sharelink:string=await this.getShortLink(url);
        console.log('短链接',sharelink);
        //return  sharelink;
        const result:IResponse = await  net.doRequest({
            data:{
                activityCode,
                page:apiresource.miniprogrampage,
                scene:sharelink
            }
        });
        //if(result.)
        if(result.data && result.data.result)
        {
            //如果没有二维码
            if(!result.data.result.weChatQrPicUrl)
            {
                alert('合成失败')
            }
            else{
                imgShareConfig.bgImg.src=result.data.result.weChatQrPicUrl;
                //获取本地分享方法;
                let fun:Function=this.getShareFunctionSocial();
                fun.call(this,imgShareConfig.bgImg.src,opt.shareChancel)
            }
        }
        else{
            alert('小程序码合成失败')
        }
    }
    doAppShare(opt:IAppShare=null){
        //获取分享社交的方法
       let fun:Function=this.getShareFunctionSocial();
       opt=Object.assign(this.app.shareconfig.snAppShare,opt);
       console.log(opt);
       console.log("fun",fun);
       if(fun)
       {
           fun.call(null,opt.shareImg,opt.shareChancel)
       }
    }
    //执行分享给好友
    doShareFriend(opt:IAppShare){
        switch(opt.toPath)
        {
            //分享到微信
            case IAPP_SHARE_PATH.weichat:

            //分享到小程序    
                break;
            case IAPP_SHARE_PATH.weichatminiprogram:break;
        }
    }
    //执行分享到朋友圈
    doShareTimeLine(opt:IAppShare){
        switch(opt.toPath)
        {
            case IAPP_SHARE_PATH.weichat:
                
                break;
            case IAPP_SHARE_PATH.weichatminiprogram:break;
        }
        //(<any>window).SNNativeClient.callMediaShare((shareImgUrl), shareChannel);
    }
    share(opt:IAppShare=null)
    {
        this.doShare(opt);
    }
    //点击页面总分享UI触发
    makeShare(opt:IAppShare=null)
    {
        //if(opt)
         switch(this.shareType)
         {
             case APPSHARETYPE.towechat:
                    
                //生成qrcode 二维码 并与背景图片合成 
                const sharelink:string=opt?opt.sharelink:"";
                this.imgdrawer.getQrcode(sharelink).then(url=>{
                    //生成分享链接二维码
                    console.log({url})
                    console.log(this.app.shareconfig.snAppShare);
                    const config:IAppShare = this.app.shareconfig.snAppShare;
                    const {x,y}=config;
                    //绘制base64二维码
                    this.imgdrawer.drawBase64(url,x,y).then((opt)=>{
                        //将二维码绘制在大背景上
                        try{
                            
                            const lastimg:string=this.imgdrawer.drawElement(opt).toData;
                            console.log({lastimg})
                            imgShareConfig.bgImg.src = lastimg;
                            (<any> window).SNSHARE.showShare();
                        }
                        catch(e)
                        {
                            console.log('二维码图片合成绘制失败，绘制图像时可能遇到了跨域问题，请联系sdk开发方');
                            console.log('图像可能来自于,', this.app.shareconfig.snAppShare.shareImg);
                            console.log('确保你当前的活动域名已经与以上图像域名做好关联');
                            (<any> window).SNSHARE.showShare();
                        }
                     
                    })
                });
               
                break;
             case APPSHARETYPE.tominiprogran:
                //弹出分享提示 
                (<any> window).SNSHARE.showShare(); //弹出分享提示 
                break;
             case APPSHARETYPE.tonative:
                //弹出分享提示
                var fun = this.getNativeFunction();
                var config:IDarwinShare = this.app.shareconfig.darwinShare; //shareApi.callNativeShare(config.title, config.desp, config.link, http(config.icon), '1,2');
                //调用本地分享事宜
                if(fun) {
                    fun.call(null, config.linkTitle, config.linkDesc, config.sharelink, config.linkIconUrl, '1,2');
                    setTimeout(function () {
                        (<any> window).SNSHARE.doShareSuccess();
                    }, 500); //弹出分享提示  
                }
                else{
                    console.log('no getNativeFunction', opt);
                    setTimeout(function () {
                        (<any> window).SNSHARE.doShareFail();
                      }, 500);
                }
                break;
         }
        //console.log(div.innerHTML);   
        //document.body.appendChild()
    }
    
    
}
export {appShare}