import { HttpRequest } from "../core/HttpRequest";
import { IRequest } from "../I/IRequest";
import { IRequestOption } from "../I/IResquestOption";
import { IResponse } from "../I/IResponse";
//
interface INetNodeRequest {
    data:any;
}
class NetNode {
    private _req:HttpRequest=new HttpRequest();
    private apilink:string;
   
    constructor(apilink:string=""){
        this.apilink=apilink;
    }
    async doRequest(params:INetNodeRequest,opt:IRequestOption=null):Promise<IResponse>{
        //拼接IRequest对象
        const _p:IRequest={
            url:this.apilink,
            method:'get',
            dataType:'json',
            data:params.data
        }
        return await this._req.request(_p,opt)
    }
    
}
export {NetNode}