import {imgShareConfig} from '../doShareConfig/AppShareConfig'
import {friend,timelineicon} from '../medias/icon'
(<any> window).shareStatistics={};
(<any> window).shareStatistics.close="clsoe";
function getTemplate(){
 
    return '\
<div class="lshare-overlay" style="overflow:hidden;position:absolute;top:0px;width:100%;display:none" >\
   <div class="lshare-box" style="width:100%;height:' + (window.innerHeight / imgShareConfig.scale) + 'px;transform:scale(' + imgShareConfig.scale + ');-webkit-transform:scale(' + imgShareConfig.scale + ');-ms-transform:scale(' + imgShareConfig.scale + ');-moz-transform:scale(' + imgShareConfig.scale + ');-o-transform:scale(' + imgShareConfig.scale + ');transform-origin:0 0;-webkit-transform-origin:0 0;-ms-transform-origin:0 0;-moz-transform-origin:0 0;-o-transform-origin:0 0;">\
       <div class="lshare-page" onclick="window.SNSHARE.hideShare()">\
           <div class="close-btn stat" name="' + ((<any>window).shareStatistics.close || '') + '"></div>\
           <div style="width:70%;height:' + (imgShareConfig.bgImg.height || 750) + 'px;overflow:hidden;position:relative;margin:30px auto;margin-bottom:10px; display:'+(imgShareConfig.bgImg.src?'block':'none')+'">' +
            '<img src="' + imgShareConfig.bgImg.src + '" width="100%">' +
'</div>\
           <div class="lshare-qrcode"></div>\
       </div>\
       <div class="share-wrap">\
       <a class="cancel-share-btn" href="javascript:void(0);"></a>\
       <div class="title1">' + imgShareConfig.shareTo + '</div>\
           <div class="qrcode-img" style="display:none">\
           <img class="qrcode-show" style="width:224px;height:224px;display:none" />\
           </div>\
           <h2 class="title2" style="display:none">' + imgShareConfig.qrcode.bottomText + '</h2>\
           <div class="lshare-btns">\
               <div class="weixin-app-message lshare-item stat" shareChannel="1" name="' + ((<any>window).shareStatistics.wx_friend || ''  ) + '   " onclick="window.SNSHARE.ShareFriend()"><i class="weixin"><img src="'+friend+'" width="100%" /></i></div>\
               <div class="weixin-timeline lshare-item stat" shareChannel="2" name="' + ((<any>window).shareStatistics.wx_timeline || '') + '" onclick="window.SNSHARE.ShareTimeLine()"><i class="pengyou"><img src="'+timelineicon+'" width="100%" /></i></div>\
           </div>\
       </div>\
   </div>\
</div>'
}
const style:string=`
.lshare-overlay{
    height:100%
}
.share-wrap{
    width:95%;
    left:2.5%;
    display:block;
    background:#eeeeee;
    overflow:hidden;
    height:200px;
    border-radius:10px;
    position:absolute!important;
    bottom:0px
}
.lshare-page{
    height:100%
}
.lshare-box{
    height:100%
}
.title1{
    margin-top:4%;
    width:100%;
    text-align:center;
    height:50px;
    
}
.lshare-btns{
    width:100%;
    display: flex;
    justify-content: space-around;
}
.lshare-item{
    height:80px;
    width:80px;
    background:#eeeeee;
    text-align:center;
    border-radius:10px;
    -webkit-border-radius:10px;
    -o-border-radius:10px;
    -ws-border-radius:10px;
    
}
`
export default ()=>{
    const styleElement:HTMLStyleElement=document.createElement('style');
    styleElement.innerHTML=style;
    document.head.appendChild(styleElement);
    return getTemplate()

}