interface IShare{
    activityCode?:string;
    //分享链接
    sharelink?:string;
}
interface IDarwinShare extends IShare{
   
    linkDesc:string;
    linkIconUrl:string;
    linkShareSwitch:string;
    linkTitle:string
}
interface IWXMiniShare extends IShare{
    shareChancel?:string;
    //小程序分享路径
    url?:string
}
enum IAPP_SHARE_CHANCEL {
    friend="1",
    timeline="2",
}
enum IAPP_SHARE_PATH {
    weichat='h5',
    weichatminiprogram='wmini'
}
interface IAppShare extends IShare{
    shareImg:string;
    shareChancel?:string;
    toPath?:string;
    x?:number;
    y?:number
}

export {
    IShare,
    IDarwinShare,
    IAppShare,
    IWXMiniShare,
    IAPP_SHARE_CHANCEL,
    IAPP_SHARE_PATH
}