import { AHero } from "./AHero";
import { application } from "../app";
import { MPShare } from "./MPShare";
import { DarwinShare } from "./DarwinShare";
import { HttpRequest } from "../core/HttpRequest";
import { apiresource } from "../config/apiresource";

class wxbridge {
    static startConfig(app:application):AHero{
        if(!wx) {console.log('微信jssdk加载失败');return};
        wxbridge.configWX().then((configoption:any)=>{
            //微信JSSDK配置
            const jsApiList:Array<string>=[
                'updateAppMessageShareData',
                'updateTimelineShareData'
            ]
            configoption={debug:true,...configoption,jsApiList}
            console.log('微信JSSDK配置');
            wx.config(configoption);
            wx.ready((res:any)=>{
                console.log('微信签名完成');
            })
            
        });
        if(wx.miniProgram && wx.__wxjs_environment=='miniprogram')
        {
            //if is mimiprogram;then return mpshare;
            return new MPShare(app);
        }
        else{
            //else return darwinshare;
            return new DarwinShare(app);
        }
        
    }
    static async configWX(){
        const request:HttpRequest=new HttpRequest();
        const api:string=apiresource.host+apiresource.wxjssdktoken;
        const result=await request.request({
            url:api,
            method:'get',
            dataType:'jsonp',
            data:{
                url:encodeURIComponent(window.location.href),
                ran:Math.random()*10,
            }
        });
        return result;
    }
    static callNativeShare(){
        
    }
}
export {wxbridge}