interface IcallbackProxy{
    cb(res:any):void
}
class callbackManaget {
    static cbindex:number=0;
    static getCallbackName(callbackProxy:IcallbackProxy):string{
        callbackManaget.cbindex++;
        const funName= `cb${callbackManaget.cbindex}`;
        const fun=function (res:any):any{
            
            callbackProxy.cb.call(null,res);
            return res;
        };
        (<any> window)[funName]=fun;
        return  funName;
    }
}
export default (fun:Function)=>{
    return callbackManaget.getCallbackName({cb:(res:any)=>{fun.call(null,res)}});
}