import { AHero } from "./AHero";
import { ShareConfig } from "../doShareConfig/ShareConfig";
import { apiresource } from "../config/apiresource";
import { application } from "../app";
import { HttpRequest } from "../core/HttpRequest";
import { IResponse, IResponseData } from "../I/IResponse";
import { IDarwinShare } from "../I/IShareOption";
class DarwinShare extends AHero{
    constructor(app:application)
    {
        super(app);
        //获取达尔文分享接口
        const urlapi=apiresource.gamehost+apiresource.getDarwinShare;
        const activityCode:string=this.app.activityCode;
        const req:HttpRequest=new HttpRequest();
        req.request({
            url:urlapi,
            dataType:'json',
            method:'get',
            data:{
                activityCode
            }
        }).then(<T>(res:T)=>{this.handleRes(res)})
    }
    handleRes<T>(res: IResponse | T):void{
       if((<IResponse>res).code=='1')
       {
           //console.log(1231231231);
           const myres:IResponseData=(<IResponse>res).data;
           const shareoption:ShareConfig=this.app.shareconfig;
           const myoption:any=shareoption.darwinShare;
           shareoption.darwinShare=myres.result;
           
       }
    }
    //
    makeShare(opt:IDarwinShare=null){
        //获取分享配置文件
        const shareoption:ShareConfig=this.app.shareconfig;
        //shareoption.darwinShare=opt;
        if(opt){shareoption.darwinShare=opt};
        opt=shareoption.darwinShare;

        const option:IDarwinShare=shareoption.darwinShare;
        //生成分享配置。
        const msgshare:IupdateAppMessageShareData={
            desc:opt.linkDesc,
            title:opt.linkTitle,
            imgUrl:opt.linkShareSwitch,
            link:window.location.href,
            success:(res:any):void=>{console.log('darwin share appmess done',res)}
        }
        const timeshare:IupdateTimelineShareData={
           
            title:opt.linkTitle,
            imgUrl:opt.linkShareSwitch,
            link:window.location.href,
            success:(res:any):void=>{console.log('darwin share timeline done',res)}
        }
        try{
            wx.updateTimelineShareData(timeshare);
            wx.onMenuShareTimeline(timeshare);
            wx.onMenuShareAppMessage(msgshare);
            wx.updateAppMessageShareData(msgshare)
        }catch(e)
        {
            console.log(e);
        }
       
    }
}
export {DarwinShare}